.section__subscribe {
  @extend %container;
}
.subscribe__img {
  height: auto;
  position: relative;

  @media screen and (min-width: 480px) {
    min-width: 320px;
    width: 280px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-bottom: 20px;
  }
  @media screen and (min-width: 768px) {
    width: 281px;

    position: absolute;
    padding-bottom: 20px;
  }
  @media screen and (min-width: 1280px) {
    width: 608px;
  }
}

.tittle__subscribe {
  font-weight: 700;
  @include font(50, 42);
  margin-bottom: 20px;
}
.discription__subscribe {
  overflow: auto;
  font-weight: 400;
  @include font(24, 16);
  margin-bottom: 24px;
}

.subscribe--input {
  width: 395px;
  @media screen and (min-width: 480px) {
    width: 278px;
  }
}
