.offerings {
    padding-top: 41px;
    padding-bottom: 60px;

    background-image: url(../images/offerings/offerings-bg-xs.svg);
    
    background-repeat: no-repeat;
    background-position: top;
    background-size: cover;
    min-width: 320px;
    height: 100%;

    @media screen and (min-width: 480px) {
            background-image: url(../images/offerings/offerings-bg-s.svg);
            min-width: 480px;
        }

    @media screen and (min-width: 768px) {
            padding-top: 150px;
            padding-bottom: 100px;

            background-image: url(../images/offerings/offerings-bg-m.svg);
            min-width: 768px;
            
        }

    @media screen and (min-width: 1280px) {
            padding-top: 177px;
            padding-bottom: 43px;

            background-image: url(../images/offerings/offerings-bg-l.svg);
            background-size: 1280px;
        }
}

.offerings__container {
    @extend %container;

    @media screen and (min-width: 1280px) {
            display: flex;
        }    
}

// apples

.offerings__aside {
    @media screen and (max-width: 1279px) {
             margin-bottom: 60px;
         }

    @media screen and (min-width: 1280px) {
            margin-right: 90px;
        }
}

.aside-offerings__canvas {
    display: block;

    width: 280px;
    height: 309px;

    background-image: url(../images/offerings/offerings-img-1@1x_sm.png);
    background-repeat: no-repeat;
    background-size: 280px 309px;
    background-position: center;

    @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
        background-image: url(../images/offerings/offerings-img-1@2x_sm.png);
    }

    @media screen and (max-width: 767px) {
        margin: 0 auto;
    }

    @media screen and (min-width: 768px) and (max-width: 1279px) {
        width: 704px;
        height: 394px;

        background-image: url(../images/offerings/offerings-img-1@1x_md.png),
            url(../images/offerings/offerings-img-2@1x_md.png),
            url(../images/offerings/offerings-img-3@1x_md.png);

        background-repeat: no-repeat;
        background-size: 357px 394px, 166px 210px, 108px 136px;
        background-position: 0 0, 386px 116px, 596px 190px;

        @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
            background-image: url(../images/offerings/offerings-img-1@2x_md.png),
                url(../images/offerings/offerings-img-2@2x_md.png),
                url(../images/offerings/offerings-img-3@2x_md.png);
        }
    }

    @media screen and (min-width: 1280px) {
        width: 550px;
        height: 604px;

        background-image: url(../images/offerings/offerings-img-1@1x_l.png);

        background-repeat: no-repeat;
        background-size: 550px 604px;
        background-position: center;

        @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
            background-image: url(../images/offerings/offerings-img-1@2x_l.png);
        }
    }
}
// content
// .offerings__content {}

.content-offerings {
    @media screen and (min-width: 1280px) {
            padding-top: 83px;
        }
 }

.content-offerings__heading {
    @include font(33px, 28px);
    color: $txt-clr;

    margin-bottom: 28px;

    @media screen and (min-width: 768px) {
        @include font(49px, 42px);
        margin-bottom: 20px;
        max-width: 526px;   
        }

    @media screen and (min-width: 1280px) {
        @include font(50px, 42px);
        }
}

.content-offerings__text {
    @include font(16px, 14px);
    color: $txt-clr;

    margin-bottom: 28px;

    @media screen and (min-width: 768px) {
            @include font(19px, 16px);
            max-width: 500px;

            margin-bottom: 40px;
        }
    
    @media screen and (min-width: 1280px) {
             @include font(24px, 16px); 
        }   
}

.content-offerings__list {
    @extend %reset-list;
    
    margin-bottom: 28px;
    
    @media screen and (min-width: 768px) {
            margin-bottom: 40px;
            gap: 116px;
            display: flex;
        }
        
    @media screen and (min-width: 1280px) {
             margin-bottom: 32px;
             gap: 67px;
        }
}
// list

// .list-content-offerings {}

.list-content-offerings__item {
    color: $txt-clr;

    @media screen and (max-width: 767px) {
            &:not(:last-child) {
                    margin-bottom: 28px;
                }
        }

    &::before {
        content: '';
        display: block;

        width: 48px;
        height: 48px;
        border-radius: 50%;

        margin-bottom: 8px;
    }

    &:nth-child(1)::before {
        content: '';
        background: url(../images/offerings/icon-rocket.svg);
        background-repeat: no-repeat;
        background-position: center;
        background-color: white;
    }

        &:nth-child(2)::before {
            content: '';
            background: url(../images/offerings/icon-touch.svg);
            background-repeat: no-repeat;
            background-position: center;
            background-color: white;
        }
}

.list-content-offerings__subtitle {
    @include font(21px, 18px);

    margin-bottom: 16px;

    @media screen and (min-width: 1280px) {
        @include font(25px, 18px); 
    }
}

.list-content-offerings__text {
    @include font(16px, 14px);

    @media screen and (min-width: 1280px) {
            @include font(21px, 14px);
    }
}

// btns

.content-offerings__buttons {
    display: flex;
    align-items: center;

    @media screen and (min-width: 1280px) {
        display: block;
        }
}

.btn--offerings {
    padding-top: 10px;
    padding-bottom: 10px;
    padding-right: 22px;
    padding-left: 22px;

    background-color: white;
    border: 2px solid $btn-border;
    transition: border 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus { 
        border: 2px solid #597D00;
    }

    @media screen and (max-width: 1279px) {
            margin-right: 24px;
        }
}

.content-offerings__link {
    @extend %reset-link;
    display: block;
    @include font(20px, 14px);

    display: flex;
    align-items: center;
    gap: 3px;

    @media screen and (min-width: 1280px) {
            display: none;
        }
}

